@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* font-family: 'Permanent Marker', cursive; */
/* font-family: 'Indie Flower', cursive; */

#root {
	height: auto;
	background-color: black;
	color: white;
}
.login-form {
	text-align: center;
	width: 100%;
	max-width: 330px;
	padding: 15px;
	margin: auto;
	height: 100%;
}
button {
	margin: 2%;
}
img {
	height: 120px;
	width: 120px;
	margin: auto;
}
#body {
	height: 70vh;
}

footer {
	position: -webkit-sticky;
	position: sticky;
	bottom: 3vh;
	display: grid;
	height: 7vh;
	grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
	background-color: rgb(136, 136, 136);
}
footer a {
	margin: auto;
}
footer img {
	display: block;
	width: 60%;
	height: auto;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: auto;
}
#profile-icon {
	grid-column-start: 1;
}
#friends-icon {
	grid-column-start: 2;
}
#home-icon {
	grid-column-start: 3;
}
#leaderboard-icon {
	grid-column-start: 4;
}
#settings-icon {
	grid-column-start: 5;
}

/* font-family: 'Permanent Marker', cursive; */
/* font-family: 'Indie Flower', cursive; */
#Heading {
	display: grid;
	grid-template-columns: 40% 60%;
	grid-template-rows: auto auto auto auto;
	height: 20vh;
	font-family: "Indie Flower", cursive;
}
#profile-picture {
	grid-column-start: 1;
	grid-row: 1 / span 4;
	width: auto;
	height: 100%;
	margin: auto;
}

#welcome {
	grid-column-start: 2;
	grid-row-start: 1;
	margin-top: auto;
	margin-bottom: auto;
	font-size: 1.5rem;
}
#nickname {
	grid-column-start: 2;
	grid-row-start: 2;
	margin-top: auto;
	margin-bottom: auto;
	font-size: 2rem;
	font-family: "Permanent Marker", cursive;
}
#rank {
	grid-column-start: 2;
	grid-row-start: 4;
	margin-top: auto;
	margin-bottom: auto;
}
#uid {
	grid-column: 2;
	grid-row: 3;
}
#edit {
	height: 2rem;
	width: 80%;
	margin: auto;
}

/* font-family: 'Permanent Marker', cursive; */
/* font-family: 'Indie Flower', cursive; */
#profile-edit {
	display: grid;
	grid-template-columns: 1fr 2fr;
	grid-template-rows: auto auto auto;
	margin-left: 3%;
	margin-right: 3%;
	font-family: "Indie Flower", cursive;
}
#profile-edit label {
	font-size: 1.1rem;
}

/* font-family: 'Permanent Marker', cursive; */
/* font-family: 'Indie Flower', cursive; */

#profile-page {
	font-family: "Indie Flower", cursive;
}
#edit-save {
	margin: auto;
	width: 30%;
}
#profile-body-tabs {
	display: grid;
	grid-template-columns: auto auto;
}

/* font-family: 'Permanent Marker', cursive; */
/* font-family: 'Indie Flower', cursive; */

.friend-stub {
	display: grid;
	grid-template-rows: auto auto auto auto;
	text-align: center;
	color: white;
}

.stub-name {
	font-family: "Permanent Marker", cursive;
	font-size: 1.5rem;
}

.stub-id {
	font-family: "Indie Flower", cursive;
}

.stub-location {
	font-family: "Indie Flower", cursive;
}

.stub-rank {
	font-family: "Indie Flower", cursive;
}

#play-button img {
	width: 100%;
	height: auto;
}

/* font-family: 'Permanent Marker', cursive; */
/* font-family: 'Indie Flower', cursive; */

#about-us {
	margin-left: 3%;
	margin-right: 3%;
}
#about-us h2 {
	font-family: "Permanent Marker", cursive;
	font-size: 3rem;
	text-align: center;
}
.developer {
	display: grid;
	grid-template-columns: 1fr 2fr;
	grid-template-rows: auto auto auto;
}
.developer img {
	display: block;
	margin: auto;
	grid-column: 1;
	grid-row: 1 / span 2;
}

.developer p {
	grid-column: 1 / span 2;
	grid-row: 3;
	text-align: justify;
	padding: 1rem;
	font-family: "Indie Flower", cursive;
}

.developer span {
	display: block;
	margin: auto;
	font-family: "Permanent Marker", cursive;
}

.dev-name {
	font-size: 2rem;
	text-decoration: underline;
}
.dev-role {
	font-size: 1.2rem;
}

