@import url("https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap");
/* font-family: 'Permanent Marker', cursive; */

@import url("https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap");
/* font-family: 'Indie Flower', cursive; */

#root {
	height: auto;
	background-color: black;
	color: white;
}
.login-form {
	text-align: center;
	width: 100%;
	max-width: 330px;
	padding: 15px;
	margin: auto;
	height: 100%;
}
button {
	margin: 2%;
}
img {
	height: 120px;
	width: 120px;
	margin: auto;
}
#body {
	height: 70vh;
}

footer {
	position: sticky;
	bottom: 3vh;
	display: grid;
	height: 7vh;
	grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
	background-color: rgb(136, 136, 136);
}
footer a {
	margin: auto;
}
footer img {
	display: block;
	width: 60%;
	height: auto;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: auto;
}
#profile-icon {
	grid-column-start: 1;
}
#friends-icon {
	grid-column-start: 2;
}
#home-icon {
	grid-column-start: 3;
}
#leaderboard-icon {
	grid-column-start: 4;
}
#settings-icon {
	grid-column-start: 5;
}
