@import url("https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap");
/* font-family: 'Permanent Marker', cursive; */

@import url("https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap");
/* font-family: 'Indie Flower', cursive; */
#Heading {
	display: grid;
	grid-template-columns: 40% 60%;
	grid-template-rows: auto auto auto auto;
	height: 20vh;
	font-family: "Indie Flower", cursive;
}
#profile-picture {
	grid-column-start: 1;
	grid-row: 1 / span 4;
	width: auto;
	height: 100%;
	margin: auto;
}

#welcome {
	grid-column-start: 2;
	grid-row-start: 1;
	margin-top: auto;
	margin-bottom: auto;
	font-size: 1.5rem;
}
#nickname {
	grid-column-start: 2;
	grid-row-start: 2;
	margin-top: auto;
	margin-bottom: auto;
	font-size: 2rem;
	font-family: "Permanent Marker", cursive;
}
#rank {
	grid-column-start: 2;
	grid-row-start: 4;
	margin-top: auto;
	margin-bottom: auto;
}
#uid {
	grid-column: 2;
	grid-row: 3;
}
#edit {
	height: 2rem;
	width: 80%;
	margin: auto;
}
